.navs {
    position: absolute;
    z-index: 1;
    color: #FFFAFA;
    bottom:0;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-bottom: 20px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }

  #myVideo {
    position: relative;
    min-width: 100%;
    min-height: 100%;
    width: 100%;
    height: 100%;
    z-index: 0;
  }

  .nav {
      color: #FFFAFA;
      -ms-flex-align: center;
          align-items: center;
      text-align: center;
      -ms-flex-pack: center;
          justify-content: center;
      margin-left: auto;
      margin-right: auto;
  }

  .nav-link {
      font-size: larger;
      text-align: center;
      -ms-flex-pack: center;
          justify-content: center;
      margin-left: auto;
      margin-right: auto;
  }

  a {
    color: #f1f1f1;
    text-decoration: none;
    list-style: none;
  }

  a:hover {
    color:black;
  }

  #navs li {
    list-style-type: none;
  }

  #title {
    font-size: 50px;
    font-family: 'Alfa Slab One', cursive;
    z-index:1;
    position: absolute;
    color:  #FFFAFA;
    margin: 0 auto;
    margin-left: 40%;
    margin-top: 25%;
  }

  .grow:hover
{
        -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
        transform: scale(1.3);
}



